@import "./_job.less";

@import "./module/fn-m-job-floating-layer.less";

@job-img-dir: 'job/_images';

.reset-pass{
	width:96%;
	.margin(3%);
	overflow:hidden;
}

.new-pass{
	line-height:45*@rex;
	background:#fff;
	border-top:1*@rex solid #D5D5D5;
	border-bottom:1*@rex solid #D5D5D5;
	position:relative;
	overflow:hidden;

	.pa-ss{
		display:block;
		width:120*@rex;
		float:left;
		text-align:right;
		color:#666666;
		padding-right:10*@rex;
	}

	input[type=text]{
		float:left;
		color:#999999;
		border:none;
		line-height:45*@rex;
	}

	.chose{
		display:block;
		background: data-uri('@{job-img-dir}/land.png');
		background-size: 100% 100%;
		.size(16*@rex);

	}

	.job-input{
		position:relative;
	}

	.iconfont{
		color:#cfcfcf;
		// .absolute(0,45*@rex,14*@rex,_);
		position:absolute;
		bottom: 14*@rex;
		right:45*@rex;
		.size(16*@rex);
	}

	.eye{
		display:block;
		background: data-uri('@{job-img-dir}/eye.png');
		background-size: 100% 100%;
		.size(20*@rex,13*@rex);
		.absolute(16*@rex,12*@rex,16*@rex,_);

		&.open{
			background-image: data-uri('@{job-img-dir}/eye-open.png');
		}
	}

}

a.btn{
	display:block;
	width:94%;
	.margin(5%,3%,_,3%);
	background:#C20053;
	border-radius:4*@rex;
	line-height:40*@rex;
	color:#fff;
	border:none;
	text-align:center;
}

.pass-pro{
	.size(300*@rex,70*@rex);
	background:#565656;
	border-radius:6*@rex;
	margin:0 auto;
	color:#fff;
	line-height:70*@rex;
	text-align:center;
	font-size:16*@rex;
	margin-top:25*@rex;
}

